<template>
    <div class="projects-container">
        <ul class="nav-selector">
            <li class="active all">all</li>
            <li class="beginner">html | css</li>
            <li class="intermediate">html | css | javascript</li>
            <li class="intermediate-js-api">html | css | javascript | api</li>
            <li class="intermediate-api">javascript | api</li>
        </ul>
        <div class="container">
            <div class="intermediate intermediate-js-api intermediate-api">
                <img src="../assets/weather.png" alt="weather" title="weather">
                <span data-link="https://mostafamohamed123456.github.io/Weather/">weather <i class="fas fa-link"></i></span>
            </div>
            <div class="intermediate">
                <img src="../assets/todo-list.png" alt="todo-list" title="todo-list">
                <span data-link="https://mostafamohamed123456.github.io/To-Do-list-/">todo-list <i class="fas fa-link"></i></span>
            </div>
            <div class="intermediate">
                <img src="../assets/memory-game.jpg" alt="memory game" title="memory game">
                <span data-link="https://mostafamohamed123456.github.io/Memory-Game/">memory game<i class="fas fa-link"></i></span>
            </div>
            <div class="intermediate">
                <img src="../assets/creative.png" alt="creative" title="creative">
                <span data-link="https://mostafamohamed123456.github.io/Creative/">creative <i class="fas fa-link"></i></span>
            </div>
            <div class="intermediate">
                <img src="../assets/calculator.png" alt="calculator" title="calculator">
                <span data-link="https://mostafamohamed123456.github.io/calculator/">calculator <i class="fas fa-link"></i></span>
            </div>
            <div class="beginner">
                <img src="../assets/blogr.png" alt="blogr" title="blogr">
                <span data-link="https://mostafamohamed123456.github.io/Blogr/">blogr <i class="fas fa-link"></i></span>
            </div>
            <div class="intermediate intermediate-js-api intermediate-api">
                <img src="../assets/profiles.png" alt="profiles" title="profiles">
                <span data-link="https://mostafamohamed123456.github.io/profiles/">profiles <i class="fas fa-link"></i></span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name:"projects",
        methods:{
            showHideProjects:function(){
                let navListItems = document.querySelectorAll(".nav-selector li");
                let containerProjects = document.querySelectorAll(".container div");
                containerProjects.forEach((projLink)=>{
                    projLink.children[1].addEventListener('click',()=>{
                        window.open(projLink.children[1].getAttribute("data-link"),"_blank")
                    })
                })
                navListItems.forEach((item,index)=>{
                    item.onclick=()=>{
                        containerProjects.forEach((project)=>{
                            //check if the project class name exist in the listItem class name
                            if(!project.classList.contains(item.getAttribute("class"))){
                                if(item.classList.contains("all")){
                                project.classList.remove("hide");
                                setTimeout(()=>{
                                    project.style.transform="scale3d(1,1,1)"
                                },200)
                                }else{
                                    project.style.transform="scale3d(0,0,1)"
                                    setTimeout(()=>{
                                        project.classList.add("hide")
                                    },200)
                                    
                                }
                                
                            }else{
                                project.classList.remove("hide");
                                setTimeout(()=>{
                                    project.style.transform="scale3d(1,1,1)"
                                },200)
                            }
                            
                        })
                        //remove all class active from other listItems
                        navListItems.forEach((listItem)=>{
                            listItem.classList.remove("active")
                        });
                        //add class active to clicked listItem
                        item.classList.add("active");
                    }
                });
            },
        },
        mounted(){
            this.showHideProjects();
        }
    }
</script>

<style lang="scss" scoped></style>